<template>
  <div><input type="file" @change="upload"></div>
</template>
<script>
import OSSUploader from '@/utils/oss/uploader';

const uploader = new OSSUploader();


export default {
  methods: {
    upload(e) {
      const file = e.target.files[0];
      uploader.send({
        sourceType: 'MATERIAL',
        body: file,
      });
    },
  },
};

</script>
